export default {
    /**
     *  state
     *
     * modelstate
     * 1. 
     * 2. statemodel
     * */
    state: {
        name: '',
        user: null,
        syncObj: {
            good: 123,
            bar: {
                a: {
                    aa: 'aa',
                    aaa: 'aaa',
                    aaaa: ['a', 'a1', 'a2'],
                },
            },
            foo: ['f', 'f1'],
        },
    },
    /**
     *  state  localStorage
     * false
     * 1. true model state 
     * 2. [path, path, ...] jsonpathhttps://lodash.com/docs/4.17.15#get
     */
    syncLocal: ['syncObj.bar.a.aaaa[1]', 'syncObj.foo', 'name', 'user'],
    /**
     *  syncLocalsessionStorage
     */
    // syncSession: true,

    /**
     * tryhandleError
     *  true
     */
    errorTip: {
        async: true, // 
        sync: true, // 
        getUser: '', // getUser 
    },

    /**
     * falseerrorTip
     */
    successTip: {
        async: false, // 
        sync: false, // 
        getUser: '', // getUser  ""
        setUser: '',
    },
    /**
     * 
     * false
     * 1. truemodelredux-undo
     * 2. object https://github.com/omnidan/redux-undo
     *      includeexclude redux-undo filterinclude
     *
     *  undoable
     * 1. stateconnectmethod
     * demo = {
     *   future: []
     *   group: null
     *   index: 4
     *   limit: 5
     *   past: (4) [{}, {}, {}, {}]
     *   present: {name: 1618027715626, ...}
     * }
     * 2. model
     * `${modelName}Undo`  demoUndo
     * `${modelName}Redo`
     * `${modelName}Jump`
     * `${modelName}JumpToPast`
     * `${modelName}JumpToFuture`
     * `${modelName}ClearHistory`
     * */
    undoable: {
        // https://github.com/omnidan/redux-undo
        include: ['setName'],
        exclude: ['setOptions'], // include exclude includeexclude
        limit: 5,
    },
    /**
     * true
     * 
     * 1. true 
     * 2. ['getUser'] getUser
     */
    // debounce: [
    //     // 'testAsync',
    // ],
    /**
     *  state
     * ActionType: action_user_setName action__
     * @param name 
     * @param state model state 
     * @returns {{name}} 1.  state2. falsity state
     */
    setName(name, state) {
        console.log('', name, state);
        return { name };
    },
    setOptions: (options) => {
        console.log('setOptions ');
        return { options };
    },

    /**
     * promise
     *  state.getUserLoadingstate.getUserError 
     * ActionType: action_user_getUser_resolve action_user_getUser_reject action_user_getUser_padding
     * debouncestate
     * @param id
     * @param state
     * @returns {Promise<{name: number, age: number}>} promise resolve 
     */
    async getUser(id, state) {
        console.log('getUser state', state);
        return new Promise((resolve, reject) => {
            setTimeout(() => {
                Math.random() > 0.5 ? resolve({ user: { name: 123, age: 23 } }) : reject(new Error(''));
            }, 2000);
        });
    },
    testAsync: async (time) => {
        return new Promise((resolve, reject) => {
            setTimeout(() => {
                resolve({ asyncResult: `${time}` });
            }, time);
        });
    },
};
