import ajax from 'src/commons/ajax';
import {getLoginUser, isLoginPage, formatMenus, getContainerId} from '@ra-lib/admin';
import {isNoAuthPage} from 'src/commons';
import {IS_SUB} from 'src/config';

export default {
    /**
     * 
     * @returns {Promise<*[]|*>}
     */
    async getMenuData() {
        // 
        if (isNoAuthPage()) return [];

        // 
        if (IS_SUB) return [];

        // 
        // return (this.getMenuData.__CACHE =
        //     this.getMenuData.__CACHE ||
        //     ajax
        //         .get('/authority/queryUserMenus', { userId: getLoginUser()?.id })
        //         .then((res) => res.map((item) => ({ ...item, order: item.order ?? item.ord ?? item.sort })))
        //         .catch(() => []));

        // 
        return [
            {id: 5, title: '', order: 999, type: 1},
            /*-*/ {id: 51, parentId: 5, title: '', path: '/subjectManage/mySubject', order: 800, type: 1},
            /*-*/ {id: 52, parentId: 5, title: '', path: '/subjectManage/depaSubject', order: 800, type: 1},
            /*-*/ {id: 53, parentId: 5, title: '', path: '/subjectManage/allSubject', order: 800, type: 1},
            {id: 6, title: '', order: 998, type: 1},
            /*-*/ {id: 61, parentId: 6, title: '', path: '/indexManage/myIndex', order: 800, type: 1},
            /*-*/ {id: 62, parentId: 6, title: '', path: '/indexManage/depaIndex', order: 800, type: 1},
            /*-*/ {id: 63, parentId: 6, title: '', path: '/indexManage/myIndex/viewDetails', order: 800, type: 2},
            {id: 7, title: '', order: 800, type: 1},
            /*-*/ {id: 71, parentId: 7, title: '', path: '/indexTask/taskPublish', order: 800, type: 1},
            /*-*/ {id: 72, parentId: 7, title: '', path: '/indexTask/taskApprove', order: 800, type: 1},
            /*-*/ {id: 73, parentId: 7, title: '', path: '/indexTask/myTask', order: 800, type: 1},
            {id: 8, title: '', order: 997, type: 1},
            /*-*/ {id: 81, parentId: 8, title: '', path: '/indexWarehouse/publicWarehouse', order: 800, type: 1},
            /*-*/ {id: 82, parentId: 8, title: '', path: '/indexWarehouse/modelWarehouse', order: 800, type: 1},
            {id: 9, title: '', order: 996, type: 1},
            /*-*/ {id: 91, parentId: 9, title: '', path: '/warningManage/newWarning', order: 800, type: 1},
            /*-*/ {id: 92, parentId: 9, title: '', path: '/warningManage/historyMange', order: 800, type: 1},
            /*-*/ {id: 93, parentId: 9, title: '', path: '/warningManage/ignoreList', order: 800, type: 1},
            {id: 1, title: '', order: 1, type: 1},
            /*-*/ {id: 11, parentId: 1, title: '', path: '/system/users', order: 900, type: 1},
            /*-*/ {id: 12, parentId: 1, title: '', path: '/system/roles', order: 900, type: 1},
            /*-*/ {id: 13, parentId: 1, title: '', path: '/system/menus', order: 900, type: 1},
            /*-*/ {id: 14, parentId: 1, title: '', path: '/system/depart', order: 900, type: 1},
            {id: 2, title: '', path: '/', order: 10000000000, type: 1},
        ];
    },
    /**
     * 
     * @returns {Promise<T[]>}
     */
    async getMenus() {
        // mockmock
        if (process.env.REACT_APP_MOCK) await new Promise((resolve) => setTimeout(resolve));

        const serverMenus = await this.getMenuData();
        const menus = serverMenus
            .filter((item) => !item.type || item.type === 1)
            .map((item) => {
                return {
                    ...item,
                    id: `${item.id}`,
                    parentId: `${item.parentId}`,
                };
            });
        return formatMenus(menus);
    },
    /**
     * 
     * @returns {Promise<*>}
     */
    async getCollectedMenus() {
        // 
        if (isLoginPage()) return [];

        // 
        if (IS_SUB) return [];

        const loginUser = getLoginUser();
        const data = await ajax.get('/authority/queryUserCollectedMenus', {userId: loginUser?.id});
        // const data = [];

        const menus = data.filter((item) => item.type === 1).map((item) => ({...item, isCollectedMenu: true}));

        return formatMenus(menus);
    },
    /**
     * 
     * @param menuId
     * @param collected
     * @returns {Promise<void>}
     */
    async saveCollectedMenu({menuId, collected}) {
        await ajax.post('/authority/addUserCollectMenu', {userId: getLoginUser()?.id, menuId, collected});
    },
    /**
     * 
     * @returns {Promise<*[string]>}
     */
    async getPermissions() {
        const serverMenus = await this.getMenuData();
        return serverMenus.filter((item) => item.type === 2).map((item) => item.code);
    },
    /**
     * 
     * @returns {Promise<*[{title, name, entry}]>}
     */
    async getSubApps() {
        // 
        const menuTreeData = (await this.getMenus()) || [];

        // 
        const loginUser = getLoginUser();
        const props = {
            mainApp: {
                loginUser: loginUser,
                token: loginUser?.token,
            },
        };
        let result = [];
        const loop = (nodes) =>
            nodes.forEach((node) => {
                const {_target, children} = node;
                if (_target === 'qiankun') {
                    const {title, name, entry} = node;
                    const container = `#${getContainerId(name)}`;
                    const activeRule = `/${name}`;

                    result.push({
                        title,
                        name,
                        entry,
                        container,
                        activeRule,
                        props,
                    });
                }
                if (children?.length) loop(children);
            });
        loop(menuTreeData);

        return result;
    },
};
