import moment from 'moment';

const now = moment().format('YYYY-MM-DD HH:mm:ss');

export default `
    create table if not exists menus
    (
        id        INTEGER PRIMARY KEY,
        parentId  INTEGER                             null,
        title     varchar(50)                         null,     -- comment '',
        icon      varchar(50)                         null,     -- comment '',
        basePath  varchar(200)                        null,     -- comment '',
        path      varchar(200)                        null,     -- comment '',
        target    varchar(50)                         null,     -- comment 'menu  qiankun  iframe iframe _self  _blank ',
        sort INTEGER   default 0                 null,     -- comment '',
        type      INTEGER   default 1                 not null, -- comment ' 1  2 ',
        enabled    tinyint(1)                          not null, -- comment '',
        code      varchar(50)                         null,     -- comment '',
        name      varchar(50)                         null,     -- comment '',
        entry     varchar(200)                        null,     -- comment '',
        createdAt timestamp default CURRENT_TIMESTAMP not null, -- comment '',
        updatedAt timestamp default CURRENT_TIMESTAMP null,     -- comment '',
        constraint menus_id_uindex
            unique (id)
    );
    create table if not exists user_collect_menus
    (
        id        INTEGER PRIMARY KEY,
        userId    INTEGER                             not null,
        menuId    INTEGER                             not null,
        createdAt timestamp default CURRENT_TIMESTAMP not null,
        updatedAt timestamp default CURRENT_TIMESTAMP not null,
        constraint user_collect_menus_id_uindex
        unique (id)
        );

    create table if not exists role_menus
    (
        id        INTEGER PRIMARY KEY,
        roleId    INTEGER                             not null,
        menuId    INTEGER                             not null,
        createdAt timestamp default CURRENT_TIMESTAMP not null,
        updatedAt timestamp default CURRENT_TIMESTAMP not null,
        constraint role_menus_id_uindex
            unique (id)
    );

    create table if not exists roles
    (
        id        INTEGER PRIMARY KEY,
        type      INTEGER,
        systemId      INTEGER,
        enabled    tinyint(1)                          not null, -- comment '',
        name      varchar(50)                         not null, -- comment '',
        remark    varchar(200)                        null,     -- comment '',
        createdAt timestamp default CURRENT_TIMESTAMP not null, -- comment '',
        updatedAt timestamp default CURRENT_TIMESTAMP null,     -- comment '',
        constraint roles_id_uindex
            unique (id)
    );

    create table if not exists user_roles
    (
        id        INTEGER PRIMARY KEY,
        userId    INTEGER                             not null,
        roleId    INTEGER                             not null,
        createdAt timestamp default CURRENT_TIMESTAMP not null,
        updatedAt timestamp default CURRENT_TIMESTAMP not null,
        constraint user_roles_id_uindex
            unique (id)
    );

    create table if not exists users
    (
        id        INTEGER PRIMARY KEY,
        account   varchar(50)                         not null, -- comment '',
        name      varchar(50)                         not null, -- comment '',
        password  varchar(20)                         null,     -- comment '',
        mobile    varchar(20)                         null,     -- comment '',
        email     varchar(50)                         null,     -- comment '',
        userDepartId     varchar(50)                         null,     -- comment '',
        userDepartName     varchar(50)                         null,     -- comment '',
        enabled    tinyint(1)                          not null, -- comment '',
        createdAt timestamp default CURRENT_TIMESTAMP not null, -- comment '',
        updatedAt timestamp default CURRENT_TIMESTAMP not null,
        constraint users_account_uindex
            unique (account),
        constraint users_id_uindex
            unique (id)
    );

    create table if not exists subjectManage
    (
        id        INTEGER PRIMARY KEY,
        zhuantimingcheng   varchar(50)                         not null, -- comment '',
        zhuantifuzebumen      varchar(50)                         not null, -- comment '',
        fuzeren  varchar(20)                         null,     -- comment '',
        xiangguanbumen    varchar(20)                         null,     -- comment '',
        fuzerenlianxifangshi    varchar(20)                         null,     -- comment '',
        zhengcelianjie    varchar(200)                         null,     -- comment '',
        zhuantimiaoshu    varchar(200)                         null,     -- comment '',
        zhuantizhibiaoshu     varchar(50)                         null,     -- comment '',
        createdAt timestamp default CURRENT_TIMESTAMP not null, -- comment '',
        updatedAt timestamp default CURRENT_TIMESTAMP not null,
        constraint subjectManage_id_uindex
            unique (id)
    );
    create table if not exists depart
    (
        id        INTEGER PRIMARY KEY,
        bumengmingcheng   varchar(50)                         not null, -- comment '',
        bumengbianhao      varchar(50)                         not null, -- comment '',
        beizhu  varchar(100)                         null,     -- comment '',
        parentid    varchar(20)                         null,     -- comment '',
        parentName    varchar(110)                         null,     -- comment '',
        createdAt timestamp default CURRENT_TIMESTAMP not null, -- comment '',
        updatedAt timestamp default CURRENT_TIMESTAMP not null,
        constraint depart_id_uindex
            unique (id)
    );
    create table if not exists indexTable
    (
        id        INTEGER PRIMARY KEY,
        status   varchar(50)                          null, -- comment '',
        name   varchar(50)                         not null, -- comment '',
        sourceDepart      varchar(50)                          null, -- comment '',
        sourceKeshi  varchar(100)                         null,     -- comment '',
        fuzeDepart    varchar(20)                         null,     -- comment '',
        fuzeren    varchar(110)                         null,     -- comment '',
        phone    varchar(110)                         null,     -- comment '',
        meaning    varchar(110)                         null,     -- comment '',
        tongjifangshi    varchar(110)                         null,     -- comment '',
        tongjizhouqi   varchar(110)                         null,     -- comment '',
        month   varchar(110)                         null,     -- comment '',
        week   varchar(110)                         null,     -- comment '',
        day   varchar(110)                         null,     -- comment '',
        isShore   varchar(110)                         null,     -- comment '',
        shujuduijiejiekou   varchar(110)                         null,     -- comment '',
        zhibiaozhidanwei   varchar(110)                         null,     -- comment '',
        region   varchar(110)                         null,     -- comment '',
        zidingyiweidufenzu   varchar(110)                         null,     -- comment '',
        zhibiaojisuanluoji   varchar(110)                         null,     -- comment '',
        zhibiaopeizhixiang   varchar(110)                         null,     -- comment '',
        yujingyuzhi   varchar(110)                         null,     -- comment '',
        createdAt timestamp default CURRENT_TIMESTAMP  null, -- comment '',
        updatedAt timestamp default CURRENT_TIMESTAMP  null,
        constraint depart_id_uindex
            unique (id)
    );
`;
