export function arraytotree(arr) {
    var top = [],
        sub = [],
        tempObj = {};
    arr.forEach((item) => {
        if (item.parentid == '0') {
            // 
            top.push(item);
        } else {
            sub.push(item); // 
        }
        item.children = []; // children
        tempObj[item.id] = item; // idkeytempObj
    });
    sub.forEach((item) => {
        // 
        var parent = tempObj[item.parentid] || {children: []};
        // children
        parent.children.push(item);
    });
    return top;
}
